@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
    font-family: 'Mulish', sans-serif;
}
.menu-active {
    color: green;
    border-bottom: 1px solid green;
}

.navbar {
    background-color: transparent !important;
}

.navbar-brand {
    font-size: 1.9rem;
    text-transform: capitalize !important;
}

.navbar a {
    font-size: 1.3rem;
    text-transform: capitalize;
}

.navbar a:hover {
    color: #3498db !important;
}

.menu_active {
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

#header {
    width: 100%;
    height: 80vh;
}

#hedaer h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}

#header .brand-name {
    color: #3498db;
}

#header h2 {
    color: #484848;
    font-size: 18px;
}

#header .btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-get-started:hover {
    background: #3498db;
    color: #fff;
}

.header-img {
    text-align: right;
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
} 

img.card-img-top {
    height: 200px;
    object-fit: cover;
    margin: 0 auto -40px;

}

.card-body {
    margin: 0 35px;
    background: #fff;
    position: relative;
    z-index: 1;
    padding: 32px 25px;
    border: #f2f2f2 solid 1px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 8px 20px;
    text-align: center;
}

.card {
    border-radius: 0;
    border: 0;
}

.card-text {
    display: -webkit-box;
    max-width: 100%;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.form-group {
    margin-bottom: 20px;
}

footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px 0px;
    text-align: center;
}

footer p {
    padding: 0;
    margin: 0;
}

label {
    margin-bottom: 10px;
}

.btn-primary {
    background-color: #3498db !important;
    border-color: #3498db !important;
}
